/* DISABLE DESKTOP VERSION */

#admin_board_members_page_header{
	display: none;
}

#admin_board_members_page_main{
	display: none;
}

/* HEADER */
#admin_board_members_page_header_mobile{
	display: flex;
	position: sticky;
	top: 0;
	left: 0;
	box-sizing: border-box;
	background-color: black;
}

#admin_board_members_page_header_mobile_nav{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content:space-around;
	align-items: center;
	background-color: inherit;
}

#admin_board_members_page_header_mobile_nav_img{
	height: 90%;
	width: 100px;
	border: solid white 2px;
}

#admin_board_members_page_header_mobile_nav_h1{
	color: var(--fourth-color);
}

#admin_board_members_page_header_mobile_nav_btn{
	border: none;
	background-color: inherit;
	color: var(--fourth-color);
	padding: 1%;
	cursor: pointer;
	border-radius: 5px;
	font-size: 30px;
}
#admin_board_members_page_header_mobile_nav_btn:hover{
	background-color: white;
	color: black;
}

#mobile-menu{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 2;
	height: 100vh;
	width: 100vw;
	background-color: black;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	display: none;
}

#mobile-menu-close{
	width: 100%;
	box-sizing: border-box;
	font-size: 30px;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	padding-right: 2%;
	cursor: pointer;
	color: var(--fourth-color);
}
#mobile-menu-close:hover{
	color: white;
}

#mobile-menu-sign-out-div{

	height: 80vh;
	width: 100%;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
}

#mobile-menu-sign-out-div-a{
	border: solid var(--fourth-color) 1px;
	padding: 2%;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
	color: var(--fourth-color);
}
#mobile-menu-sign-out-div-a:hover{
	border: solid white 1px;
	background-color: white;
	color: black;
}



/* MAIN */
#admin_board_members_page_main_mobile{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;

	width: 100%;
	box-sizing: border-box;
	min-height: 100vh ;
	padding-top: 2%;
	background-color: var(--third-color);
}

#admin_board_members_page_main_div_mobile{

}

#admin_board_members_page_main_mobile_div_loading{
	
}

#admin_board_members_page_main_mobile_h2{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
	width: 80%;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;

}

#admin_board_members_page_main_mobile_h21{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
	width: 80%;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

#admin_board_members_page_main_mobile_section{

	width: 100%;
	box-sizing: border-box;
display: flex;
flex-direction: column;
justify-content: flex-start;
align-items: center;}

#admin_board_members_page_main_mobile_section_nav{

	width: 98%;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	background-color: black;
	border-radius: 5px;
	height: 5vh;
}

.admin_board_members_page_main_mobile_section_nav_div_h5{

	margin-right: 1%;

	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	padding: 1%;
	color: white;
	border-bottom: solid white 1px;
	cursor: pointer;
}
.admin_board_members_page_main_mobile_section_nav_div_h5:hover{
	background-color: white;
	border-bottom: solid white 1px;
	color: black;
}
.admin_board_members_page_main_mobile_section_nav_div_h5_selected{
	background-color: white;
	border-bottom: solid white 1px;
	color: black;
}


#admin_board_members_page_main_mobile_section_nav_div_h51{
	color: var(--fourth-color);
	background-color: var(--main-color);
	border-bottom: none;
	cursor: default;
}
#admin_board_members_page_main_mobile_section_nav_div_h51:hover{
	color: var(--fourth-color);
	background-color: var(--main-color);
	border-bottom: none;
	cursor: default;
}

#admin_board_members_page_main_mobile_section_ul1{
	height: 50vh;
	
	overflow: scroll;
	width: 80%;
	padding-top: 2%;
	padding-bottom: 3%;
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}
#admin_board_members_page_main_mobile_section_ul1::-webkit-scrollbar {
  width: 8px;
}
/* scroll bar thumb (the actual scroll) */
#admin_board_members_page_main_mobile_section_ul1::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
#admin_board_members_page_main_mobile_section_ul1::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

.admin_board_members_page_main_mobile_section_ul1_li{

	margin-bottom: 2%;
	box-sizing: border-box;
}

.admin_board_members_page_main_mobile_section_ul1_li_a{
	height: 100%;
	width: 100%;

	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	text-decoration: none;
	box-sizing: border-box;
}

.admin_board_members_page_main_mobile_section_ul1_li_a_span1{

	padding: 2%;
	box-sizing: border-box;
	white-space: nowrap;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
	background-color: var(--fourth-color);
	color: var(--third-color);
	border-right: solid 4px var(--third-color);
}
.admin_board_members_page_main_mobile_section_ul1_li_a_span1:hover{
	background-color: white;
	color: black;
	border-right: solid 4px black;
}

.admin_board_members_page_main_mobile_section_ul1_li_a_span2{
	padding: 2%;
	box-sizing: border-box;
	white-space: nowrap;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	background-color: rgb(64, 68, 106);
	color: white;
}


